import React, { Component } from "react";
import { Map, GoogleApiWrapper, Marker } from "google-maps-react";
import Autocomplete from "react-google-autocomplete";
const mapStyles = {
  width: "90%",
  height: "300px",
  margin: "10px auto"
};
class MapVendor extends Component {
  state = {
    selected: {
      lat: 0,
      lng: 0
    },
    hideMap: false
  };
  componentDidMount = () => {
    this.setState({
      selected: {
        lat: this.props.lat,
        lng: this.props.lng
      }
    });
  };
  current = () => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(position => {
        this.setState({
          lat: position.coords.latitude,
          long: position.coords.longitude
        });
      });
    }
  };
  mapClickHandler = (t, map, coord) => {
    this.setState({
      selected: {
        lat: coord.latLng.lat(),
        lng: coord.latLng.lng()
      }
    });
    this.props.selectArea(this.state.selected);
  };
  onPlaceSelected = place => {
    console.log(place);
    this.map.setState({
      currentLocation: {
        lat: place.geometry.location.lat(),
        lng: place.geometry.location.lng()
      }
    });
  };
  render() {
    return (
      <>
        <div
          style={{
            width: "100%",
            display: "flex",
            alignItems: "center",
            justifyContent: "center"
          }}
        >
          <Autocomplete
            style={{
              width: "90%",
              height: "40px",
              paddingLeft: "16px",
              margin: "0px auto",
              marginTop: "2px"
            }}
            onPlaceSelected={this.onPlaceSelected}
            types={["(regions)"]}
          />
        </div>
        <Map
          google={this.props.google}
          zoom={14}
          style={mapStyles}
          onClick={this.mapClickHandler}
          onCenterChanged={(e, t, d) => {
            console.log(e);
            console.log(t);
            console.log(d);
          }}
          initialCenter={{
            lat: this.props.lat,
            lng: this.props.lng
          }}
          ref={ref => (this.map = ref)}
        >
          <Marker
            position={{
              lat: this.state.selected.lat,
              lng: this.state.selected.lng
            }}
          />
        </Map>
      </>
    );
  }
}
export default GoogleApiWrapper({
  apiKey: "AIzaSyB7qtP77Rx3HmirB5lyWJ90RYeMu4TVnyc"
})(MapVendor);
